






















import {Component, Vue} from 'vue-property-decorator';
import {Button, Toast, Radio, RadioGroup, Overlay} from 'vant';
import CertShow from '@/components/cert_card.vue';
import ScanSign from '@/components/scan_sign.vue';
import {CashboxJs} from '@/util/cashboxjs';

import 'vant/lib/button/style';
import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/radio/style';
import 'vant/lib/radio-group/style';
import 'vant/lib/overlay/style';
import {CertOwn, CertSignStatus} from '../../model/certs_model';
import {notes, NoteType} from '@/util/note';
import {ToastUtil} from "../../util/toast";
import {DiamondUtil} from "../../util/diamond_util";

Vue.use(Button).use(Radio).use(RadioGroup).use(Overlay);

@Component({
    components: {CertShow, ScanSign},
})
export default class Prepare extends Vue {
    private certOwn!: CertOwn;
    private isLoadingAccount = false;        // 加载数据时的遮罩层

    public created(): void {
        {
            (window as any).nativeSignMsgToJsResult = this.nativeSignMsgToJsResult;
        }
        this.certOwn = this.$store.state.nowCertOwn;
        this.$store.state.title = "证书信息签名";
    }

    public onClickToSign(): void {
        this.isLoadingAccount = true;
        //let nativeAddress = "5DHob83qDK5KWgz9xeqXcyYpku7bnLWR2cuRzKQpkY4p7BzL";  // todo 测试用： 业务员 freda 身份
        DiamondUtil.getInstance().assembleAddCertInfo(this.certOwn.certGia.gia, this.certOwn, this.$store.state.nativeChainAddress).then((res) => {
            console.log("res===>" + res);
            this.isLoadingAccount = false;
            if (this.$store.state.nativeChainAddress && this.$store.state.nativeChainAddress !== "") {
                this.$store.commit('updateCertBusinessAddress', this.$store.state.nativeChainAddress);//记录本人地址
            } else {
                ToastUtil.show('当前业务员信息为空，不知道有那个业务员提交的证书!' + this.$store.state.nativeChainAddress);
                return;
            }
            CashboxJs.getInstance().callNativeSignMsgToJs(res);
        }).catch((e) => {
            this.isLoadingAccount = false;
            ToastUtil.fail('拼装数据出现错误!');
            console.log("assembleAddCertInfo===>" + e.toString());
        });
    }

    public nativeSignMsgToJsResult(val: string): void {
        ToastUtil.show('签名步骤，已完成');
        this.$store.commit('updateNowCertSignedInfo', val);
        this.$store.commit('updateNowCertSignState', CertSignStatus.Success);
        let tThis = this;
        setTimeout(function () {
            tThis.$router.push('/frame/cert/confirm');
        }, 1000);
    }
}
